<template>
	<view class="page">
		<!-- 地址 -->
		<view class="address-data">
			<view class="address-list" v-if="address" @click="gopage('/pages/address/index?order_num='+order_num)">
				<view class="list">
					<text>{{address.mainaddress}}</text>
				</view>
				<view class="list">
					<text class="address">{{address.address}}</text>
				</view>
				<view class="list">
					<text>{{address.name}}</text>
					<text>{{address.phone}}</text>
				</view>
				<view class="list">
					<text class="tips">(请认真核验邮寄地址)</text>
				</view>
			</view>
			<view class="address-list" v-if="!address" @click="gopage('/pages/address/add')">
				<view class="addressbtn">添加收货地址</view>
			</view>
			<view class="bar">

			</view>
		</view>
		<!-- 商品 -->
		<view class="goods-data">
			<view class="goods-title">
				<text>商品信息</text>
			</view>
			<view class="goods-list">
				<view class="list">
					<view class="thumb">
						<image :src="goods.imgurl" mode=""></image>
					</view>
					<view class="item">
						<view class="title">
							<text class="name one-omit">{{goods.title}}</text>
							<text class="attr">规格：{{order.sku}}</text>
						</view>
						<view class="price-number">
							<view class="price">
								<text class="min">￥</text>
								<text class="max">{{order.price}}</text>
							</view>
							<view class="number">
								<text>x {{order.num}}</text>
							</view>
						</view>
						<!-- <view class="tag">
							<text>支持七天无理由退货</text>
						</view> -->
					</view>
				</view>
			</view>
			<view class="delivery">
				<div class="list">
					<view class="title">配送</view>
					<view class="content">
						<text>快递运输</text>
						<text class="iconfont icon-more"></text>
					</view>
				</div>
				<div class="list">
					<view class="title">留言</view>
					<view class="content">
						<input type="text" placeholder="选填,建议先和商家沟通确认" v-model="order_msg">
					</view>
				</div>
			</view>
		</view>
		<!-- 优惠 -->
		<!-- <view class="discounts-data">
			<view class="discounts">
				<div class="list">
					<view class="title">积分</view>
					<view class="content">
						<text>共300，满1000可用</text>
					</view>
				</div>
			</view>
		</view> -->
		<!-- 订单金额 -->
		<view class="order-price">
			<view class="price-list">
				<view class="list">
					<view class="title">
						<text>商品金额</text>
					</view>
					<view class="price">
						<text>￥{{order.price}}</text>
					</view>
				</view>
				<view class="list">
					<view class="title">
						<text>兑换分抵扣</text>
					</view>
					<view class="price">
						<text>-{{order.integral}}</text>
					</view>
				</view>
				<view class="list">
					<view class="title">
						<text>运费</text>
					</view>
					<view class="price">
						<text class="highlight">+￥0.00</text>
					</view>
				</view>
				<view class="list">
					<view class="title">
						<text>运费险</text>
					</view>
					<view class="price">
						<text class="highlight">+￥0.00</text>
					</view>
				</view>
			</view>
		</view>
		<!-- 地址提示 -->
		<!-- 底部合计提交 -->
		<view class="footer-submit">
			<view class="price">
				<text class="min">￥</text>
				<text class="max">{{order.price}}</text>
			</view>
			<view class="submit" @click="onSubmit">
				<text>提交订单</text>
			</view>
		</view>
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				scrollTop: 0,
				order_num:'',
				order:[],
				goods:[],
				address:[],
				order_msg:'',
			};
		},
		onPageScroll(e){
			this.scrollTop = e.scrollTop;
		},
		onLoad(e) {
			this.order_num = e.order_num;
		},
		onShow() {
			this.loadData()
		},
		methods:{
			loadData() {
				let that = this;
				that.req({
					url: 'checkorder',
					data: {order_num:that.order_num},
					Loading: true,
					success: function(res) {
						console.log(res)
						that.order = res.data.order;
						that.goods = res.data.goods;
						that.address = res.data.address;
					}
				})
			},
			gopage(path){
				uni.navigateTo({
					url:path
				})
			},
			/**
			 * 提交订单
			 */
			onSubmit(){
				let that = this;
				that.req({
					url: 'setordermsg',
					data: {order_num:that.order_num,order_msg:that.order_msg,address_id:that.address.id},
					Loading: true,
					success: function(res) {
						console.log(res)
						if(res.code == 1){
							uni.navigateTo({
								url: '/pages/detail/payment?order_num='+res.data
							})
						}else{
							uni.showToast({
								title: res.msg,
								icon: 'none',
								duration: 2000
							})
						}
					}
				})
				/* uni.redirectTo({
					url: '/pages/CashierDesk/CashierDesk',
				}) */
			},
      /**
       * 跳转点击
       * @param {String} type 跳转类型
       */
      onSkip(type){
        switch (type){
          case 'address':
            uni.navigateTo({
              url: '/pages/AddressList/AddressList',
            })
            break;
        }
      }
		}
	}
</script>

<style scoped lang="scss">
	@import 'payinfo.scss';
</style>
